/* custom dropdown */
.fc-dropdown--trigger:hover{
	cursor: pointer;
}

.fc-dropdown{
	opacity: 0;
	height:0px;
	overflow: hidden;
	border-radius: 2px;
	position: absolute;
	width:100px;
	background:#FFFFFF;
	box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.11);
	z-index:99999;
	-webkit-animation-duration: 0.1s;
	-moz-animation-duration: 0.1s;
	animation-duration: 0.1s; 	
}
.fc-dropdown__submenu{
	position: absolute;
    background: #FFF;
    right: 160px;
    width: 145px;
    padding: 15px;
    opacity: 0;
    height:0px;
    overflow: hidden;
    margin-top: -22px;
    border-radius: 2px;
	box-shadow: 1px 1px 2px 2px rgba(0, 0, 0, 0.11);
}
.fc-dropdown__submenu--trigger:hover .fc-dropdown__submenu{
	opacity: 1;
	height:auto;
	overflow: visible;
}
.fc-dropdown__submenu li{
	padding: 5px 15px !important;
}
.fc-dropdown__title{
    font-weight: 600;
    font-size: 10px;
    text-transform: uppercase;
    display: block;
    text-align: center;
    border-bottom: 1px solid #DDD;
    padding: 2px 5px 7px;
    color: #505458;
    letter-spacing: 1px;
    font-family: "open sans";
}
.fc-dropdown--open{
	opacity: 1;
	height:auto;
	overflow: visible;
}
